*{
  padding: 0;
  margin: 0;
  line-height: 1;
  box-sizing: border-box;
}
.container{
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  padding-top: calc((100vh - 816px)/2);
}
#header,#main{
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: .625rem;
  background-color: white;
  height: 816px;
  box-shadow: 0px 0px 4px gray;
}
#header{
  margin-right: 1.5rem;
  width: 360px;
}
#main{
  width: 1200px;
}
header,main{
  height: auto;
  height: 100%;
}
header{
  width: 100%;

}
main{
  width: 100%;
  /* background-color: dodgerblue; */
  /* visibility: hidden; */
}
.top, .bottom{
  z-index: 5;
  width: 100%;
  height: 2rem;
  box-shadow: 0px 0px 4px gray;
}
@media screen and (max-width: 768px){
  #header{
    width: 300px;
    margin-right: 1rem;
  }
  #main{
    width: 900px;
  }
}

